html {
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}

* {max-height: 1000000px;}
body{
    margin:0;
    font:12px/15px Arial, Helvetica, sans-serif;
    color:#000;
    -webkit-text-size-adjust:100%;
    -ms-text-size-adjust: none;
}
img{
    border-style:none;
    vertical-align:top;
}
a{
    color:#4b4b80;
    text-decoration:underline;
}
a:hover{text-decoration:none;}
a:active {background-color: transparent;}
input, textarea, select{
    font:100% Arial, Helvetica, sans-serif;
    vertical-align:middle;
    color:#1e1b1b;
}
main, header, footer, article, section, hgroup, nav, figure, aside { display: block; }
figure{
    margin:0;
    padding:0;
}
form, fieldset{
    margin:0;
    padding:0;
    border-style:none;
}
input[type=submit],
input[type=text],
input[type=password],
input[type=file],
textarea{-webkit-appearance:none;}
textarea,
input[type=submit],
input[type=text]{-webkit-border-radius:0;}
input[type=image],
button{
    padding:0;
    border:none;
}
input[type="submit"]::-moz-focus-inner{border:0}
input[type="submit"]{cursor:pointer;}

#header{
    background-image: url(../img/bg-header.png);
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}
#header .container{
    padding: 21px 199px 0 199px;
}
.nav_row{
    margin: 0 0 275px 0;
}
#logo{
    float: left;
}
#nav{
    float: right;
    margin: 29px 0 0 0;
}
#nav ul{
    list-style: none;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
}
#nav ul li{
    display: inline-block;
    margin: 0 0 0 30px;
    position: relative;
}
#nav ul li a{
    color: #fff;
    text-decoration: none;
    font: 14px/17px Lato Bold, Arial, Helvetica, sans-serif;
}
#pull{
    display: block;
    float:right;
    width:12px;
    height:11px;
    position:relative;
    border-radius:3px;
    text-indent:-9999px;
    overflow:hidden;
}
#pull:before,
#pull:after,
#pull span{
    background: #fff;
    position:absolute;
    top: 6px;
    left:21%;
    right:20%;
    height:2px;
    margin-top:-3px;  height/2 
    -webkit-transition:all 0.2s linear;
    transition:all 0.2s linear;
}
#pull:before,
#pull:after{
    content:'';
    top: 3px;
}
#pull:after{top: 9px;}
.submenu{
    list-style: none;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 20px;
    right: -5px;
    display: none;
}
#nav .submenu li{
    display: block;
    margin: 0;
}
.title_row{
    margin: 0 0 242px 0;
}
.title_sec{
    text-align: center;
    color: #fff;
    margin: 0 0 0 80px;
}
.title_sec h1{
    font:italic bold 36px/39px Times New Roman, Arial, Helvetica, sans-serif;
    margin: 0;
    color: #e0d7d8;
}
.title_sec h2{
    font:bold 100px/104px Lato Regular, Arial, Helvetica, sans-serif;
    margin: 0 0 30px 0;
    text-transform: uppercase;
}
.get_start{
    text-decoration: none;
    color: #fff;
    border: 3px solid;
    padding: 22px 79px 27px;
    font: bold 18px/21px Lato Regular, Arial, Helvetica, sans-serif;
    text-transform: uppercase;
    border-radius: 3px;
    margin: 0 0 40px 0;
    display: inline-block;
}
.title_sec p {
    margin: 0;
    text-transform: uppercase;
    font: 14px/17px Lato Regular, Arial, Helvetica, sans-serif;
}
.title_sec p a{
    color: #fff;
    font: bold 14px/17px Lato Bold, Arial, Helvetica, sans-serif;
}
.block_box{
    padding: 101px 209px 98px 209px;
}
.block_box_sec{
    text-align: center;
    padding: 0 13px 0 13px;
}
.box_inspired,
.box_rewarded,
.box_social{
    width: 104px;
    height: 104px;
    border: 2px solid;
    border-radius: 55px;
    position: relative;
    margin: 0 0 21px 130px;
}
.box_inspired:after,
.box_rewarded:after,
.box_social:after{
    content: '';
    width: 88px;
    height: 1px;
    background: #f57365;
    position: absolute;
    left: 7px;
    top: 150px;
}
.box_rewarded img{
    position: absolute;
    top: 36px;
    left: 20px;
}
.box_inspired img,
.box_social img{
    position: absolute;
    top: 20px;
    left: 20px;
}
.block_box_sec h1{
    margin: 0;
    font: 18px/21px Lato Bold, Arial, Helvetica, sans-serif;
    position: relative;
    margin: 0 0 30px 0;
}
.block_box_sec p{
    margin: 0;
    font: 16px/19px Lato Light, Helvetica,  Arial,sans-serif;
}
.block_container{
    background: #f7f7f7;
    padding: 102px 0 0 0;
}
.block_cont_box{
    padding: 83px 0 0 0;
    position: relative;
}
.block_cont_box:before{
    content: '';
    width: 88px;
    height: 1px;
    background: #f57365;
    position: absolute;
    left: 0px;
    top: 115px;
}
.block_cont_box h1{
    margin: 0 0 28px 0;
    font: 18px/22px "Lato Bold", Helvetica, Arial,sans-serif;
}
.block_cont_box p{
    margin: 0 0 35px 0;
    font: 16px/24px "Lato Light", Helvetica, Arial,sans-serif;
    color: #676767;
}
.block_cont_box a{
    margin: 0 10px 0 0;
}
.block_slider{
    background-image: url(../img/bg-slider.png);
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}
.block_slider .container{
    padding: 80px 0 80px 0;
}
.sld_box{
    text-align: center;
}
.sld_box q{
    display: block;
    font: 30px/36px 'Lato Hairline', Arial, Helvetica, sans-serif;
    color: #b3adb2;
    margin: 0 0 20px 0;
}
.sld_box cite{
    font: 18px/36px 'Lato Hairline Italic', Arial, Helvetica, sans-serif;
    color: #b3adb2;
}
.block_images .container{
    padding: 100px 0 60px 0;
}
.inscription{
    position: relative;
}
.inscription h1{
    margin: 0 0 40px 0;
    text-align: center;
    font: 18px/30px "Lato Bold",Arial, sans-serif, Helvetica;
    text-transform: uppercase;
}
.inscription:after{
    content: '';
    width: 88px;
    height: 1px;
    background: #f57365;
    position: absolute;
    top: 35px;
    left: 745px;
}
.img_list{
    list-style: none;
    padding: 0 0 60px 0;
    margin: 0;
    letter-spacing: 0;
    font-size: 0;
    line-height: 0;
}
.img_list li {
    display: inline-block;
    width: 20%;
    text-align: center;
    position: relative;
}
.img_list img{
    z-index: 0;
    position: relative;
}
.title{
    margin: 0 auto -40px auto;
    z-index: 2;
    position: relative;
    top: 90px;
}
.title h1{
    font:italic bold 15.37px/12.81px Times New Roman, Arial,sans-serif,Helvetica;
    letter-spacing: 1px;
    color: #fff;
    margin: 0 0 13px 0;
    position: relative;
}
.title h2{
    font: 36px/12.81px "Lato Bold", Arial,sans-serif,Helvetica;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #fff;
    margin: 0;
    position: relative;
}
.num_list{
    text-align: center;
}
.num_list ul{
    display: table;
    width: 100%;
    list-style: none;
    margin: 0;
    padding: 0 210px;
}
.num_list li{
    vertical-align: top;
    display: table-cell;
}
.num_list ul li h1{
    font: 36px/30px "Lato Bold",Arial,Helvetica,sans-serif;
    margin: 0 0 16px 0;
    padding: 0;
}
.num_list ul li p{
    margin: 0;
    font: 16px/18px "Lato Light", Arial,Helvetica,sans-serif;
}
#footer{
    background: #f7f7f7;
}
#footer .container{
    padding: 41px 209px;
}
.copyright{
    font: 14px/18px "Lato Light", Arial,Helvetica,sans-serif;
    color: #8b8b8b;
}
.link{
    float: right;
    display: inline-block;
    font: 14px/18px "Lato Light", Arial,Helvetica,sans-serif;
}
.link a{
    font: 14px/18px "Lato Regular", Arial,Helvetica,sans-serif;
    color: #000;
    text-decoration: none;
}


#header:after,
.block_box:after,
.block_container:after,
.clear:after{
    content: '';
    width: 100%;
    clear: both;
    display: table;
}